:root {
    font-size: 0.625;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Lato", Arial, Helvetica, sans-serif;
}

body {
    height: 100vh;
}

body * {
    /* border: 1px solid red; */
}

body {
    display: grid;
    grid-template-columns: minmax(30vh, 1fr) 6fr;
    grid-template-rows: 0.8fr 4fr;
}

body > div:first-child {
    position: fixed;
    z-index: 1;
}

.add-to-do, .add-project {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
}

.add-to-do-form {
    background-color: rgb(254, 246, 244);
    opacity: 1;
    width: 60vmin;
    height: 45vmin;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
}

.add-project-form {
    position: relative;
    background-color: rgb(254, 246, 244);
    opacity: 1;
    width: 45vmin;
    height: 20vmin;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
}

.add-project-form > div:first-of-type {
    margin-left: 15px;
    font-size: calc(.5rem + 1.2vh);
    margin-bottom: 10px;
}

.add-project-form > div:last-child > form {
    margin-left: 15px;
    display: flex;
    justify-content: space-between;
}

.add-project-form button {
    position: relative;
    top: 4vmin;
    right: 1vmin;
    appearance: none;
    padding: 5px 10px;
    border: none;
    border-radius: 10px;
    background-color: #e2e2e2;
}

.add-to-do-form > span + div {
    position: absolute;
    height: 10vmin;
    width: 10vmin;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
