b(206, 206, 206);\n}\n\n.add-project-form input:invalid + span {\n    position: absolute;\n    top: -20px;\n    right: 5px;\n}\n\n.add-to-do-form input:invalid + span {\n    position: absolute;\n    left: 75%;\n}\n\n.add-project-form input:invalid + span::before, .add-to-do-form input:invalid + span::before {\n    content: \"*required\";\n    color: red;\n    font-size: calc(.5rem + .5vh);\n}\n\n.add-project-form input::placeholder, .add-to-do-form input::placeholder {\n    color: rgb(182, 177, 177);\n}\n\n.add-project-form input:invalid, .add-to-do-form input:invalid {\n    border: 1px solid red;\n}\n\n.add-to-do-form > span::before, .add-project-form > span::before {\n    position: absolute;\n    content: \"✖\";\n    top: 0;\n    right: 6px;\n    cursor: pointer;\n}\n\n.add-to-do-form > div{\n    font-size: calc(.5rem + 1vh);\n    height: 100%;\n    background-color: rgb(254, 246, 244);\n    margin-left: 15px;\n}\n\n.add-to-do-form > div div {\n    display: flex;\n    width: 50%;\n    flex-direction: column;\n    position: relative;\n}\n\n.add-to-do-form form {\n    margin-top: 1vmin;\n    padding: 1.2vmin;\n    gap: 0.8vmin;\n    height: 80%;\n    display: flex;\n    flex-direction: column;\n}\n\n.add-to-do-form select {\n    appearance: none;\n    background-color: #e2e2e2;\n    border-radius: 10px;\n    border: 1px solid #e2e2e2;\n    padding: 5px 10px;\n    outline: none;\n}\n\n\n.side-bar {\n    font-weight: bold;\n    color: white;\n    padding: 20px;\n    grid-column: 1;\n    grid-row: 1 / span 2;\n    background-color: #00aeef;\n    display: grid;\n    grid-template-rows: 2fr 1fr 0.4fr 1.8fr 1fr 10fr;\n}\n\n.icon {\n    display: flex;\n    align-items: flex-end;\n}\n\n.icon > div {\n    height: 100px;\n    width: 100px;\n    border-radius: 50%;\n    background-color: rgb(81, 81, 81);\n    background-image: url(\"./img/avatar.png\");\n    background-size: contain;\n}\n\n.welcome {\n    display: flex;\n    align-items: center;\n    font-size: calc(.8rem + 1.