.2vh);\n    color: #cfcfcfb5;\n}\n\n.header > div:last-child {\n    font-weight: bold;\n    flex: 1;\n    display: flex;\n    \n}\n\n.header > div:last-child > div {\n    font-size: calc(.8rem + 1.5vh);;\n    display: flex;\n    padding-bottom: 5px;\n    justify-content: center;\n    align-items: flex-end;\n    \n}\n\n.header > div:last-child > div > div {\n    display: flex;\n    gap: 2px;\n    align-items: center;\n}\n\n.header img {\n    height: calc(.8rem + 1.5vh);\n    \n}\n\n.title {\n    flex: 1;\n}\n\n.desc {\n    flex: 2;\n}\n\n.due {\n    flex: 1;\n}\n\n.priority {\n    flex: 1;\n}\n\n.add {\n    flex: .55;\n}\n\n.add img {\n    cursor: pointer;\n    position: absolute;\n    height: calc(3.5rem + 2vh);\n    background-color: #00aeef;\n    border-radius: 50%;\n}\n\n.add img:hover {\n    background-color: #0293c8;\n    box-shadow: 2px 2px 4px rgb(200, 200, 200);\n}\n\n.add img:active {\n    background-color: #0079a5;\n}\n\n.main-content {\n    overflow: scroll;\n    grid-column: 2;\n    grid-row: 2;\n    background-color: #b1eaff;\n    padding: 45px;\n    display: flex;\n    flex-direction: column;\n    gap: 45px;\n}\n\n.to-do {\n    display: flex;\n    height: 60px;\n    flex: 0 0 auto;\n}\n\n.to-do-bar {\n    display: flex;\n    width: 90%;\n    background-color: #00aeef;\n    border-radius: 10px;\n    position: relative;\n    box-shadow: 2px 2px 8px rgb(153, 153, 153);\n}\n\n.delete {\n    position: absolute;\n    right: 5px;\n    top: 2px;\n}\n\n.delete::before {\n    content: \"✖\";\n    cursor: pointer;\n}\n\n.delete:hover {\n    color: red;\n}\n\n.delete:active {\n    top: 3px;\n    color: rgb(208, 0, 0);\n}\n\n.to-do-bar > div {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    text-align: center;\n    color: white;\n    font-weight: bold;\n    font-size: calc(.8rem + 1vh);\n    gap: 2px;\n}\n\n.to-do-title {\n    flex: 1;\n}\n\n.to-do-desc {\n    flex: 2;\n}\n\n.to-do-due {\n    flex: 1;\n}\n\n.to-do-priority {\n    fl